import { Controller } from 'react-hook-form';
import { InputCustomStyled } from './InputCustom.styled';

const InputCustom = ({control, name, ...rest}) => {
    return(
        <div>
            <Controller 
                control={control}
                name={name}
                defaultValue=""
                render={({field}) => (
                    <InputCustomStyled {...rest} type='text' {...field} />
                )}
            />
        </div>
    );
};

export default InputCustom;